@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,300i,400,700);
body {
  overflow-x: hidden;
}

#root {
  max-width: 100%;
  overflow-x: hidden;
}

.my-button {
  font-size: 1em;
  line-height: normal;
  text-transform: uppercase;
  font-weight: 600;
  transition: all .3s ease-in-out, color 0.2s 0.3s;
  border-radius: 45px;
  height: auto;
  padding: .8em 2.1em;

  color: rgba(255, 103, 68, 0.91);
  border: 2px solid rgba(255, 103, 68, 0.91) !important;
  background-color: #fff;
}

.my-button.border-white {
  font-size: 1.4em !important;
  border-color: white !important;
}

.my-button:hover {
  cursor: pointer;
  padding: .8em 2.7em;
  background-color: rgba(255, 103, 68, 0.91) !important;
  color: #fff;
  text-decoration: none;
}

.my-button:active,
.my-button:focus,
.my-button.active {
background-image: none;
outline: 0;
box-shadow: none;
}

svg {
  color: #888;
  margin-top: 5px;
  margin-right: 13px;
  margin-left: 13px;
  transition: 0.5s;
}
svg:hover {
  color: #111;
}

.projectCard {
  margin-bottom: 40px;
  margin-right: auto;
  margin-left: auto;
  max-width: 500px;
}

.projectCard > div > div {
  display: flex;
  flex-direction: column;
  text-align: justify;
  margin: 0;
}

.projectCard  img {
  border-bottom: 0.5px solid lightgray;
  padding-top: 5px;
  padding-right: 5px;
  padding-left: 5px;
}

.projectCard h3 {
  text-align: center;
  font-weight: bolder;
  margin-top: 8px;
}

.projectCard a {
  margin-right: auto;
  margin-left: auto;
}

.projectCard a > .phone {
  display: none;
}

.projectCard a > .notPhone {
  display: block;
}

@media (max-width: 425px) {
  .projectCard a > .phone {
    display: block;
  }
  .projectCard a > .notPhone {
    display: none;
  }
}
.projects {
  justify-content: center;
  background: #ffffff;
  padding: 50px;
}

@media (max-width: 425px) {
  .projects {
    padding: 25px;
  }
}

.projects h1 {
  color: #ff7b44;
  font-size: 40px;
  font-weight: 700;
  text-align: center;
}

.projects > .phone {
  display: none;
}

.projects > .notPhone {
  display: block;
}

@media (max-width: 425px) {
  .projects > .phone {
    display: block;
  }
  .projects > .notPhone {
    display: none;
  }
}

.projects > p {
  color: gray;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 40px;
}
.Card_card__10yt9 {
  padding: 20px;
  margin: 0;
}

.Card_card__10yt9 img {
  width: 100%;
}

